import { createHashRouter } from "react-router-dom";
import App from "./App";
import Home from "./routes/Home";
import LandingNote from "./routes/LandingNote";
import Solutions from "./routes/Solutions";

/**
 * ghpage   react-router-dom createHashRouter 
 * createHashRouter  root url # 
 *  createBrowserRouter  
 */
const router = createHashRouter([
    {
        path: "/",
        element: <App />,
        children: [
            {
                path: "notes",
                element: <LandingNote />,
                children: [
                    {
                        path: "solutions",
                        element: <Solutions />, 
                    },
                ],
            },{
                path: "",
                element: <Home />,
            },
        ],
    }
]);


export default router;